.body-404 {
  height: 100vh;
  background-color: #141019;
  background: radial-gradient(at 50% -20%, #908392, #0d060e) fixed;
  display: flex;
  align-items: center;
  justify-content: center;
}

.a {
  display: block;
  -webkit-transform: scale(4);
          transform: scale(4);
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.container {
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.mb-5,
.my-5 {
  margin-bottom: 3rem !important;
}

.mt-5,
.my-5 {
  margin-top: 3rem !important;
}

@media (min-width:768px) {
  .col-mb-12 {
    flex: 0 0 100%;
    max-width: 100%;
  }
}

#handboy {
  -webkit-animation: swing ease-in-out 1.3s infinite alternate;
          animation: swing ease-in-out 1.3s infinite alternate;
  -webkit-transform-origin: 98% 98%;
          transform-origin: 98% 98%;
  transform-box: fill-box;

}


#girllight {
  -webkit-animation: swing ease-in-out 1.3s infinite alternate;
          animation: swing ease-in-out 1.3s infinite alternate;
  -webkit-transform-origin: 0% 97%;
          transform-origin: 0% 97%;
  transform-box: fill-box;
}

#hairgirl {
  -webkit-animation: swinghair ease-in-out 1.3s infinite alternate;
          animation: swinghair ease-in-out 1.3s infinite alternate;
  -webkit-transform-origin: 60% 0%;
          transform-origin: 60% 0%;
  transform-box: fill-box;

}

#zero {
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
  transform-box: fill-box;

}

/*************swing************/
@-webkit-keyframes swing {
  0% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
  }

  100% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
  }
}
@keyframes swing {
  0% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
  }

  100% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
  }
}


/*************swing hair************/
@-webkit-keyframes swinghair {
  0% {
    -webkit-transform: rotate(6deg);
            transform: rotate(6deg);
  }

  100% {
    -webkit-transform: rotate(-6deg);
            transform: rotate(-6deg);
  }
}
@keyframes swinghair {
  0% {
    -webkit-transform: rotate(6deg);
            transform: rotate(6deg);
  }

  100% {
    -webkit-transform: rotate(-6deg);
            transform: rotate(-6deg);
  }
}
