.App {
    background-color: rgba(0, 0, 0, 0.08);
}

.top-header {
    height: 40px;
    padding-left: 4%;
    padding-right: 4%;
    display: block;
    /* background-color: #f5f5f5; */
    line-height: 40px;
}

.top-header .inner {
    width: 88%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.button-group {
    float: right;
    margin-top: 5px;
}

.main-header {
    background-color: #ffffff00 !important;
    height: 60px;
    line-height: 60px;
    width: 88%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    align-items: center;
}

.App-logo {
    height: 60px;
    width: 60px;
    float: left;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.title {
    line-height: 64px !important;
    display: inline;
    color: #03A9F4 !important;
    font-family: 'Slidefu', monospace;
}

.search-input {
    width: 400px !important;
    line-height: 64px;
    margin-top: 17px;
    margin-left: 37px;
}

.main-menu {
    padding-left: 10% !important;
    padding-right: 10% !important;
    background-color: rgba(193, 205, 216, 0.175) !important;
}

.form {
    --width-of-input: 300px;
    --border-height: 1px;
    --border-before-color: rgba(221, 221, 221, 0.39);
    --border-after-color: #5891ff;
    --input-hovered-color: #4985e01f;
    position: relative;
    width: var(--width-of-input);
    margin: 20px;
    margin-left: 100px;
}

.input {
    font-size: 0.9rem;
    background-color: transparent;
    width: 100%;
    box-sizing: border-box;
    padding-inline: 0.5em;
    padding-block: 0.7em;
    border: none;
    border-bottom: var(--border-height) solid var(--border-before-color);
}

.input-border {
    position: absolute;
    background: var(--border-after-color);
    width: 0%;
    height: 2px;
    bottom: 5px;
    left: 0;
    transition: 0.3s;
}

.input:hover {
    background: var(--input-hovered-color);
}

.input:focus {
    outline: none;
}

.input:focus~.input-border {
    width: 100%;
}

.btn {
    background: #5cbdfd;
    font-family: inherit;
    padding: 0.3em 1.3em;
    font-weight: 600;
    font-size: 12px;
    border: 2px solid #00000050;
    border-radius: 0.4em;
    box-shadow: 0.1em 0.1em;
}

.btn:hover {
    transform: translate(-0.05em, -0.05em);
    box-shadow: 0.15em 0.15em;
}

.btn:active {
    transform: translate(0.05em, 0.05em);
    box-shadow: 0.05em 0.05em;
}